'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/useAuth';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Skeleton } from '@/components/ui/skeleton';
import { usePageBackground } from '@/hooks/usePageBackground';

export default function ProfilePage() {
  usePageBackground('https://placehold.co/1920x1080/f3e8ff/6b21a8?text=.');
  const router = useRouter();
  const { user, logout, isLoading } = useAuth();

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
    }
  }, [user, isLoading, router]);

  if (isLoading || !user) {
    return (
       <div className="max-w-2xl mx-auto">
        <Card className="shadow-lg">
          <CardHeader className="flex flex-row items-center gap-4">
             <Skeleton className="h-16 w-16 rounded-full" />
             <div className="space-y-2">
                <Skeleton className="h-6 w-48" />
                <Skeleton className="h-4 w-32" />
             </div>
          </CardHeader>
          <CardContent className="space-y-4">
             <Skeleton className="h-10 w-full" />
             <Skeleton className="h-10 w-full" />
          </CardContent>
        </Card>
       </div>
    );
  }

  const handleLogout = async () => {
    await logout();
    router.push('/');
  };
  
  const handleStartTest = () => {
    router.push('/tests');
  };
  
  const displayName = user.displayName || user.email?.split('@')[0] || 'User';
  const displayInitial = displayName.charAt(0).toUpperCase();

  return (
    <div className="max-w-2xl mx-auto">
      <Card className="shadow-lg">
        <CardHeader>
          <div className="flex items-center gap-4">
            <Avatar className="h-16 w-16">
              <AvatarImage src={user.photoURL || `https://placehold.co/100x100.png`} alt={displayName} />
              <AvatarFallback>{displayInitial}</AvatarFallback>
            </Avatar>
            <div>
              <CardTitle className="text-3xl font-headline">{displayName}</CardTitle>
              <p className="text-muted-foreground">{user.email}</p>
            </div>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <p className="text-muted-foreground">Welcome to your profile page. Here you can view your progress and manage your account.</p>
          <div className="flex flex-col sm:flex-row gap-4">
            <Button onClick={handleStartTest} className="flex-1">Start a New Test</Button>
            <Button onClick={handleLogout} variant="destructive" className="flex-1">Log Out</Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
